import Vue from 'vue'
import Router from 'vue-router'
// const _import = require('./_import_' + process.env.NODE_ENV)
// 页面导入
// const _importPage = file => _import('pages/' + file)
import login from '@/pages/login/index.vue'
import iot_visiual from '@/pages/iot_visiual/index.vue'
Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'pages',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'login',
    component:login,
    meta: {
      title: 'NB-IoT网络监测中心登陆'
    }
  }]
})
let hasAddrouter = false
//路由拦截
router.beforeEach((to, from, next) => {
  if (/login/ig.test(to.path)) {
    next()    
  } else {
    if (sessionStorage.getItem('user')) {
      if (!hasAddrouter) {
        router.addRoutes([{
          path: '/nb-iot',
          name: 'nb-iot',
          component: iot_visiual,
          meta: {
            title: 'NB-IoT 网络监测中心'
          }
        }])
        hasAddrouter = true
        next({ ...to, replace: true })
      } else {
        next()
      }
    } else {
      next('/login')
    }
  }
})
export default router
