<template>
  <div class="login_page fillcontain nb-container">
      <div class="header_title">
          <div class="tile_centet_name">
              <div class="top_img_left" ref="toprightline"></div>
              <!--<h3><span>NB-IoT</span>&nbsp;&nbsp;网络监测中心</h3>-->
              <h3><span>NB-IoT</span>&nbsp;&nbsp;Network Monitor Center</h3>
              <div class="top_img_right" ref="topleftline"></div>
          </div>
      </div>
     <!-- <page-title/> -->
    <transition name="form-fade" mode="in-out">
      <section class="form_contianer" v-show="showLogin">
        <div class="manage_tip">
          <p>NB-IOT登陆</p>
        </div>
            <input v-model="loginForm.username" placeholder="用户名" class="username" />
            <input type="password" placeholder="密码" v-model="loginForm.password" class="password" />
            <button type="primary" @click="submitForm('loginForm')" :class="'submit_btn ' + (login?'login':'')" >登陆</button>
            <div class="tip">{{showTip}}</div>
      </section>
    </transition>
  </div>
</template>

<script>
    // import {login, getAdminInfo} from '@/api/getData'
    // import {mapActions, mapState} from 'vuex'
    // import pageTitle from "../nb-lot/page-title";
    import lottie from 'lottie-web'
    import topleftline from '@/api/topleftline.json'
    import toprightline from '@/api/toprightline.json'
    export default {
          components: {
            // pageTitle,
          },
          data() {
            return {
              loginForm: {
                username: "",
                password: ""
              },
              showTip:'',
              login:false,
              rules: {
                username: [
                  { required: true, message: "请输入用户名", trigger: "blur" }
                ],
                password: [{ required: true, message: "请输入密码", trigger: "blur" }]
              },
              showLogin: false
            };
          },
          mounted() {
            this.showLogin = true;
            // if (!this.adminInfo.id) {
            // 		// this.getAdminData()
            // 	}
              var topleftlineBO =  lottie.loadAnimation({
                  animationData:topleftline,  loop:true,
                  autoplay:true,
                  renderer:'svg',
                  container: this.$refs.topleftline, // 容器 //渲染方式，有"html"、"canvas"和"svg"三种
              });
              var toprightlineBO =  lottie.loadAnimation({
                  animationData:toprightline,  loop:true,
                  autoplay:true,
                  renderer:'svg',
                  container: this.$refs.toprightline, // 容器 //渲染方式，有"html"、"canvas"和"svg"三种
              });
              topleftlineBO.play()
              toprightlineBO.play()
          },
          computed: {
            // ...mapState(['adminInfo']),
          },
          methods: {
            // ...mapActions(['getAdminData']),
            async submitForm() {
              this.login = true;
              let {username,password} = this.loginForm
              if(username == 'root' && password=='Jk300349'){
                  //动态注册路由
                  //  console.log('zhenggqu')
                   sessionStorage.setItem('user', 'root');
                   this.$nextTick(()=>{
                     this.$router.push('/nb-iot')
                   })
              }else{
                this.showTip="用户名或者密码错误！"
              }
              this.login = false;
            },
          }
          // watch: {
          // 	adminInfo: function (newValue){
          // 		if (newValue.id) {
          // 			this.$message({
          //                 type: 'success',
          //                 message: '检测到您之前登录过，将自动登录'
          //             });
          // 			this.$router.push('manage')
          // 		}
          // 	}
          // }
    };
</script>

